// eslint-disable-next-line import/no-cycle
import axios from '@axios'

export default {
  namespaced: true,
  state: {
    contracts: [],
    dates: [],
  },
  getters: {},
  mutations: {
    SET_RECORD(state, contracts) {
      state.contracts = contracts
    },
    SET_RECORD_DATES(state, dates) {
      state.dates = dates
    },
    UPDATE_RECORD(state, data) {
      const catIndex = state.contracts.findIndex(cat => cat.id === data.id)
      Object.assign(state.contracts[catIndex], data)
    },
    REMOVE_RECORD(state, itemId) {
      const caseIndex = state.contracts.findIndex(d => d.id === itemId)
      state.contracts.splice(caseIndex, 1)
    },
  },
  actions: {
    store(ctx, data) {
      return new Promise((resolve, reject) => {
        axios.post('user/contracts/create', data)
          .then(res => {
            resolve(res)
          })
          .catch(error => reject(error))
      })
    },
    active({ commit }, data) {
      return new Promise((resolve, reject) => {
        axios.post('admin/contracts/status', data)
          .then(res => {
            if (data.remove) {
              commit('REMOVE_RECORD', data.contract_id)
            }
            resolve(res)
          })
          .catch(error => reject(error))
      })
    },
    update(ctx, data) {
      return new Promise((resolve, reject) => {
        axios.put(`admin/contracts/update/${data.get('contractId')}`, data)
          .then(res => {
            resolve(res)
          })
          .catch(error => reject(error))
      })
    },
    fetch({ commit }, status) {
      return new Promise((resolve, reject) => {
        axios.get(`admin/contracts/list?status=${status}`)
          .then(response => {
            commit('SET_RECORD', response.data.data)
            resolve(response)
          })
          .catch(error => reject(error))
      })
    },
    dates({ commit }) {
      return new Promise((resolve, reject) => {
        axios.get('user/list/quran/dates')
          .then(response => {
            commit('SET_RECORD_DATES', response.data.data)
            resolve(response)
          })
          .catch(error => reject(error))
      })
    },
    show(ctx, id) {
      return new Promise((resolve, reject) => {
        axios.post('admin/contracts/show', { contract_id: id })
          .then(response => {
            // commit('SET_RECORD', response.data.data)
            resolve(response)
          })
          .catch(error => reject(error))
      })
    },
    delete({ commit }, id) {
      return new Promise((resolve, reject) => {
        axios.post('admin/contracts/destroy', { contract_id: id })
          .then(res => {
            commit('REMOVE_RECORD', id)
            resolve(res)
          })
          .catch(error => reject(error))
      })
    },

  },
}
