// eslint-disable-next-line consistent-return
router.beforeEach((to, from, next) => {
  if (to.meta.authRequired) {
    console.log('0')
    if (!auth.isAuthenticated()) {
      console.log('0000')
      return router.push({
        path: 'login',
        query: { to: to.path },
      })
    }
  }

  if (to.meta.login) {
    console.log('1')
    if (auth.isAuthenticated()) {
      console.log('2')
      if (from.name === 'home' && !to.meta.role.includes(auth.role())) {
        router.push('/contract/create')
      }
      return router.replace('/')
    }
  }

  if (auth.isAuthenticated()) {
    console.log('3')
    if (to.meta.role.includes(auth.role())) {
      console.log('4')
      return next()
    }
    if (from.name === 'home' && !to.meta.role.includes(auth.role())) {
      router.push('/contract/create')
    }
    if (auth.role() === '') {
      localStorage.clear()
      return router.replace('/login')
    }
    console.log('5')
    return router.back()
  }
  console.log('6')
  return next()
})
export default router
