Vue.filter('status', value => {
  const all = [
    {
      id: 'P',
      name: 'Pending',
    },
    {
      id: 'A',
      name: 'Accepted',
    },
    {
      id: 'R',
      name: 'Reject',
    },
    {
      id: 'C',
      name: 'Cancel by Client',
    },
  ]
  const item = all.find(x => x.id === value)
  return item ? item.name : ''
})

Vue.filter('bookingDate', value => value.split(' ')[0])

Vue.filter('dateTime', value => {
  if (value) {
    const array = value.split(':')
    return `${array[0]}:${array[1]}`
  }
  return ''
})

Vue.filter('convertTime', value => {
  const hour = parseInt(value, 10)
  let min = parseInt((value % 1).toFixed(2).substring(2), 10)
  if (min > 0) {
    min *= 0.6
  } else {
    min = '00'
  }
  return `${hour}:${min}`
})
