const localStorageKey = 'loggedIn'

class AuthService {
  // eslint-disable-next-line class-methods-use-this
  logOut() {
    localStorage.removeItem(localStorageKey)
    localStorage.removeItem('info')
  }

  // eslint-disable-next-line class-methods-use-this
  isAuthenticated() {
    // const user = JSON.parse(localStorage.getItem('info'))
    return !!(localStorage.getItem(localStorageKey) && localStorage.getItem(localStorageKey) === 'true')
  }

  // eslint-disable-next-line class-methods-use-this
  role() {
    return JSON.parse(localStorage.getItem('info')).role ? JSON.parse(localStorage.getItem('info')).role.name : ''
  }

  // eslint-disable-next-line class-methods-use-this
  userId() {
    return JSON.parse(localStorage.getItem('info')).id
  }
}

export default new AuthService()
