// eslint-disable-next-line import/no-cycle
import jwt from '@/auth/jwt/useJwt'
// eslint-disable-next-line import/no-cycle
import router from '../../router/index'

export default {
  namespaced: true,
  state: {
    userInfo: JSON.parse(localStorage.getItem('info')),
    reload: null,
  },
  getters: {},
  mutations: {
    SET_DATA(state, data) {
      state.userInfo = data
    },
  },
  actions: {
    login({ commit }, payload) {
      return new Promise((resolve, reject) => {
        jwt.login(payload)
          .then(response => {
            // If there's user data in response
            if (response.data.data
              && (response.data.data.role.name === 'admin'
                || response.data.data.role.name === 'user'
                || response.data.data.role.name === 'helper'
                || response.data.data.role.name === 'sheikh')) {
              // Set accessToken
              jwt.setToken(response.data.token)
              commit('SET_DATA', response.data.data)
              localStorage.setItem('info', JSON.stringify(response.data.data))
              localStorage.setItem('loggedIn', 'true')
              if (response.data.data.role.name === 'user') {
                router.push('/contract/create')
              } else {
                router.push(router.currentRoute.query.to || '/')
              }
              // Navigate User to homepage
              resolve(response)
            } else {
              // eslint-disable-next-line prefer-promise-reject-errors
              reject({ message: 'هناك خطا في البريد الالكتروني او كلمة المرور' })
            }
          })
          .catch(error => reject(error))
      })
    },
    register({ commit }, payload) {
      return new Promise((resolve, reject) => {
        jwt.register(payload)
          .then(response => {
            localStorage.setItem('info', JSON.stringify(response.data.data))
            router.push('/verified')
            // If there's user data in response
            if (response.data.data
              && (response.data.data.role.name === 'admin'
                || response.data.data.role.name === 'user'
                || response.data.data.role.name === 'sheikh')) {
              // Set accessToken

              router.push('/verified')
              commit('SET_DATA', response.data.data)
            } else {
              // eslint-disable-next-line prefer-promise-reject-errors
              reject({ message: 'هناك خطا في البريد الالكتروني او كلمة المرور' })
            }
          })
          .catch(error => reject(error))
      })
    },
    verified({ commit }, payload) {
      return new Promise((resolve, reject) => {
        jwt.verified(payload)
          .then(response => {
            if (response.data.data
              && (response.data.data.role.name === 'admin'
                || response.data.data.role.name === 'user'
                || response.data.data.role.name === 'sheikh')) {
              // Set accessToken
              jwt.setToken(response.data.token)
              commit('SET_DATA', response.data.data)
              localStorage.setItem('info', JSON.stringify(response.data.data))
              localStorage.setItem('loggedIn', 'true')
              // Navigate User to homepage
              if (response.data.data.role.name === 'user') {
                router.push('contract/create')
              } else {
                router.push(router.currentRoute.query.to || '/')
              }
              resolve(response)
            } else {
              // eslint-disable-next-line prefer-promise-reject-errors
              reject({ message: 'هناك خطا في البريد الالكتروني او كلمة المرور' })
            }
          })
          .catch(error => reject(error))
      })
    },
    forgetPassword(ctx, payload) {
      return new Promise((resolve, reject) => {
        jwt.forgetPassword(payload)
          .then(response => {
            resolve(response)
          })
          .catch(error => reject(error))
      })
    },
    restPassword(ctx, payload) {
      return new Promise((resolve, reject) => {
        jwt.resetPassword(payload)
          .then(response => {
            resolve(response)
          })
          .catch(error => reject(error))
      })
    },
    logout() {
      const localStorageKey = 'loggedIn'
      localStorage.removeItem(localStorageKey)
      localStorage.removeItem('info')
      router.push('/login')
    },
  },
}
