import Vue from 'vue'

// axios
import axios from 'axios'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
// eslint-disable-next-line import/no-cycle
import router from '@/router'

export const domain = 'http://api.heiratsvertrag-ig-oldenburg.de/api/'
// export const domain = 'http://127.0.0.1:8000/api/'
//
const axiosIns = axios.create({
  // You can add your headers here
  // ================================
  baseURL: domain,
  headers: {
    'Content-Type': 'application/json',
    Accept: 'application/json',
  },
  // baseURL: 'https://muze.clicktopass.com/api/',
  // baseURL: 'http://127.0.0.1:8000/api/'',
  // timeout: 1000,
})
// Add a response interceptor
axiosIns.interceptors.response.use(
  response => response,
  error => {
    if (error.response.data.message === 'Unauthenticated'
    || error.response.data.message === 'Authorization Token not found'
    || error.response.data.message === 'Token is Expired'
    || error.response.data.errors === 'Unauthenticated'
    ) {
      localStorage.removeItem('token')
      localStorage.removeItem('info')
      localStorage.setItem('loggedIn', 'false')
      // eslint-disable-next-line no-unused-expressions
      router.push('/login')
    } else if (error.response.data.errors === 'not verified') {
      localStorage.setItem('info', JSON.stringify(error.response.data.data))
      router.push({ name: 'verified' })
    }
    Vue.$toast({
      component: ToastificationContent,
      props: {
        title: error.response.data.errors,
        icon: 'CheckCircleIcon',
        variant: 'danger',
      },
    })
  },
)
Vue.prototype.$http = axiosIns

export default axiosIns
